import { styled } from '@mui/material/styles';
import { Box, Button, Grid } from '@mui/material';
import { useTheme } from '@mui/material/styles';

const layoutWidth = 1024;
const imageHeight = 450;
const IntroCardWrapper = styled(Box)(({ theme }) => ({
    display: 'flex',
    width: layoutWidth,
    padding: 40,
    [theme.breakpoints.down('md')]: {
        width: '100%',
    },
    [theme.breakpoints.down('sm')]: {
        width: '100%',
    }
}));

const IntroContentCardWrapper = styled(Box)(({ theme }) => ({
    width: '100%',
    [theme.breakpoints.down('md')]: {
        justifyContent: 'center',
        alignItems: 'center',
        textAlign: 'center',
    }
}));

const RightAlignVisionContent = ({content, image}) => {
    const theme = useTheme();
    return (
    <Grid container spacing={0} columns={16}>
        <Grid item lg={6} md={6} sm={16} xs={16}
            sx={{            
                display: 'none',
                justifyContent: 'center',
                alignItems: 'center',
                textAlign: 'center',
                height:imageHeight,
                marginBottom: 5,
                [theme.breakpoints.down('md')]: {
                    display: 'flex'
                }
            }}
        >
            <Box
                sx={{
                    width:'100%',
                    height:'100%',
                    background: 'url('+image+') center center no-repeat',
                    backgroundSize: 'contain',
                }}
            />
        </Grid>
        <Grid item lg={10} md={10} sm={16} xs={16}
            sx={{            
                display: 'flex',
                justifyContent: 'center',
                alignItems: 'center',
                textAlign: 'left',
                [theme.breakpoints.down('md')]: {
                    textAlign: 'center'
                }
            }}
        >
            {content}
        </Grid>
        <Grid item lg={6} md={6} sm={16} xs={16}
            sx={{            
                display: 'flex',
                justifyContent: 'center',
                alignItems: 'center',
                textAlign: 'center',
                minHeight:600,
                justifyContent: 'center',
                [theme.breakpoints.down('md')]: {
                    display: 'none'
                }
            }}
        >
            <Box
                sx={{
                    display: 'flex',
                    width:'100%',
                    height:'100%',
                    background: 'url('+image+') center center no-repeat',
                    backgroundSize: 'contain',
                }}
            />
        </Grid>
    </Grid>);
}

