const LeftAlignContent = ({content, image}) => {
    const theme = useTheme();
    return (
    <Grid container spacing={0} columns={16}>
        <Grid item lg={8} md={8} sm={16} xs={16}
            sx={{            
                display: 'flex',
                justifyContent: 'center',
                alignItems: 'center',
                textAlign: 'center',
                height:imageHeight,
                marginBottom: 5
            }}
        >
            <Box
                sx={{
                    width:'100%',
                    height:'100%',
                    background: 'url('+image+') center center no-repeat',
                    backgroundSize: 'contain',
                }}
            />
        </Grid>
        <Grid item lg={8} md={8} sm={16} xs={16}
            sx={{            
                display: 'flex',
                justifyContent: 'center',
                alignItems: 'center',
                textAlign: 'left',
                [theme.breakpoints.down('md')]: {
                    textAlign: 'center'
                }
            }}
        >
            {content}
        </Grid>
    </Grid>);
}

export const IntroCard = ({content, align, image, background}) => {

    const alignedContent = (align === 'left')? 
        LeftAlignContent({content,image}):
        RightAlignContent({content,image});

    return (<Box
        sx={{
            display: 'flex',
            width: '100%',
            minHeight: '600px',
            paddingBottom:10,
            justifyContent: 'center',
            alignItems: 'center',
            background: background
        }}>
        <IntroCardWrapper>   
            <IntroContentCardWrapper>
                {alignedContent}
            </IntroContentCardWrapper>
        </IntroCardWrapper>
    </Box>);
}

export const VisionCardWrapper = ({content, align, image, background}) => {

    const alignedContent = RightAlignVisionContent({content,image});

    return (<Box
        sx={{
            display: 'flex',
            width: '100%',
            minHeight: '600px',
            paddingBottom:10,
            justifyContent: 'center',
            alignItems: 'center',
            background: background
        }}>
        <IntroCardWrapper>   
            <IntroContentCardWrapper>
                {alignedContent}
            </IntroContentCardWrapper>
        </IntroCardWrapper>
    </Box>);
}
