
import React, {useRef, useEffect } from 'react';
import * as THREE from 'three';
import { GLTFLoader } from 'three/examples/jsm/loaders/GLTFLoader.js';
import { FBXLoader } from 'three/examples/jsm/loaders/FBXLoader.js';
import { OrbitControls } from 'three/examples/jsm/controls/OrbitControls.js';
import { LineMaterial } from 'three/examples/jsm/lines/LineMaterial.js';
import {Wireframe} from 'three/examples/jsm/lines/Wireframe.js';
import {WireframeGeometry2} from 'three/examples/jsm/lines/WireframeGeometry2.js';
import styled from 'styled-components/macro';
import { FontLoader } from 'three/examples/jsm/loaders/FontLoader.js';
import { TextGeometry } from 'three/examples/jsm/geometries/TextGeometry.js';



const MetaBanner = (width, height)=>{
  const renderer = new THREE.WebGLRenderer({antialias:true, alpha:true});
  renderer.setSize(width, height);

  const camera = new THREE.PerspectiveCamera( 32, width / height, 1, 500 );
  camera.position.set( 10, 0, 50 );

  const clock = new THREE.Clock();
  const scene = new THREE.Scene();
  
  const URL_PREFIX = process.env.PUBLIC_URL;  

  let mixer, controls;

  const init=()=>{
    addPlatform();
    addFont();
    addPlayer();
    addLight();
    addControls();
  }

  const addPlatform = () => {
    const gridHelper = new THREE.GridHelper( 10.5, 1 );
    gridHelper.material.color.setHex(0xD5AD6D)
    gridHelper.position.y=-10;
    scene.add(gridHelper)

    const normalMaterial = new THREE.MeshNormalMaterial()
    // Create a slippery material (friction coefficient = 0.0)
    const cubeGeometry = new THREE.BoxGeometry(9, 2, 9)
    const cubeMesh = new THREE.Mesh(cubeGeometry, normalMaterial)
    cubeMesh.position.y = -9;
    cubeMesh.castShadow = true
    scene.add(cubeMesh);
  }

  const addFont = () => {
    const fontLoader = new FontLoader();
    fontLoader.load( `${URL_PREFIX}/fonts/helvetiker_bold.typeface.json`,  (font) =>{
      const materials = [
        new THREE.MeshPhongMaterial( { color: 0x000000, flatShading: true } ), // front
        new THREE.MeshPhongMaterial( { color: 0x000000 } ) // side
      ];
      const textGeo1 = new TextGeometry( "Play with me!", {
        font: font,
        size: 90,
        height: 5,
        curveSegments: 10,
        bevelEnabled: false
      } );
      textGeo1.computeBoundingBox();
      const textMesh1 = new THREE.Mesh( textGeo1, materials );
      textMesh1.scale.x = 0.01;
      textMesh1.scale.y = 0.01;
      textMesh1.scale.z = 0.01;
      textMesh1.position.z = 4.5;
      textMesh1.position.x = -3.8;
      textMesh1.position.y = -9.5;
      scene.add( textMesh1 );

      const textGeo2 = new TextGeometry( "Together in", {
        font: font,
        size: 100,
        height: 5,
        curveSegments: 10,
        bevelEnabled: false
      } );
      textGeo2.computeBoundingBox();
      const textMesh2 = new THREE.Mesh( textGeo2, materials );
      textMesh2.scale.x = 0.01;
      textMesh2.scale.y = 0.01;
      textMesh2.scale.z = 0.01;
      textMesh2.position.z = 3.6;
      textMesh2.position.x = 4.5;
      textMesh2.position.y = -9.5;
      textMesh2.rotation.y =  Math.PI / 2;
      scene.add( textMesh2 );

      const textGeo3 = new TextGeometry( "KhoLand", {
        font: font,
        size: 100,
        height: 5,
        curveSegments: 10,
        bevelEnabled: false
      } );
      textGeo3.computeBoundingBox();
      const textMesh3 = new THREE.Mesh( textGeo3, materials );
      textMesh3.scale.x = 0.01;
      textMesh3.scale.y = 0.01;
      textMesh3.scale.z = 0.01;
      textMesh3.position.z = -4.5;
      textMesh3.position.x = 3;
      textMesh3.position.y = -9.5;
      textMesh3.rotation.y =  -Math.PI ;
      scene.add( textMesh3 );

      const textGeo4 = new TextGeometry( "Game-Meta", {
        font: font,
        size: 100,
        height: 5,
        curveSegments: 10,
        bevelEnabled: false
      } );
      textGeo4.computeBoundingBox();
      const textMesh4 = new THREE.Mesh( textGeo4, materials );
      textMesh4.scale.x = 0.01;
      textMesh4.scale.y = 0.01;
      textMesh4.scale.z = 0.01;
      textMesh4.position.z = -3.8;
      textMesh4.position.x = -4.5;
      textMesh4.position.y = -9.5;
      textMesh4.rotation.y =  -Math.PI/2 ;
      scene.add( textMesh4 );
    } );
  }

  const addPlayer = () => {
    const fbxLoader = new FBXLoader();
    fbxLoader.load( `${URL_PREFIX}/models/boy.fbx`, function ( fbx ) {
      scene.add( fbx );
      fbx.position.y=-8.5;
      fbx.scale.x = 10;
      fbx.scale.y = 10;
      fbx.scale.z = 10;
      const lookAt = fbx.position.clone()
      lookAt.x += 50;
      camera.lookAt(lookAt);
      mixer = new THREE.AnimationMixer(fbx);
      fbxLoader.load(`${URL_PREFIX}/models/hip-hop-dance.fbx`,(danceAnimation) => {
          const animationAction = mixer.clipAction(danceAnimation.animations[0])
          animationAction.play();
      });
    }, undefined, function ( error ) {
        console.error( error );
    } );
  }

  const addLight = () => {
    const ambientLight = new THREE.AmbientLight( 0xFBFAF5 );
    scene.add( ambientLight );
  }

  const addControls = () => {
    // controls
    controls = new OrbitControls( camera, renderer.domElement );
    //controls.listenToKeyEvents( window ); // optional
    controls.enableDamping = true; // an animation loop is required when either damping or auto-rotation are enabled
    controls.dampingFactor = 0.05;
    controls.screenSpacePanning = false;
    controls.minDistance = 20;
    controls.maxDistance = 45;
    controls.minPolarAngle = Math.PI /4;
    controls.maxPolarAngle = Math.PI /2;
    controls.autoRotate = true;
    controls.enableZoom = false;
    controls.autoRotateSpeed = -1;
  }

  const render = () =>{
    renderer.render(scene, camera);
  }

  const animate = () => {
    if(mixer){
      mixer.update(clock.getDelta())
    }
    if(controls){
      controls.update(); // only required if controls.enableDamping = true, or if controls.autoRotate = true
    }
    render();
  }

  const getRenderer=()=>{
    return renderer;
  }

  return {init, getRenderer, animate}
}

export const IntroWorld = () => {
  const ref = useRef();

  useEffect(() => {
    if(!ref.current){
      return;
    }

    const width = ref.current.offsetWidth;
    const height = 500;
    const metaBanner = MetaBanner(width, height);
    metaBanner.init();
    ref.current.appendChild(metaBanner.getRenderer().domElement);

    let animationNumber;
    const animate = () => {
      animationNumber = requestAnimationFrame( animate );
      metaBanner.animate();
    }

    animate();

    return () => {
        // Callback to cleanup three js, cancel animationFrame, etc
        if(ref.current){
          ref.current.innerHTML = '';
        }
        cancelAnimationFrame(animationNumber);
    }
  }, []);

  return <GrabDiv ref={ref}/>;
}

const GrabDiv = styled.div`
	color: #000;
	:hover {
		color: #ed1212;
		cursor: grab;
	}
` 